/* eslint-disable react-hooks/exhaustive-deps */
import { Fragment } from "react";
import { Hidden } from "@mui/material";

// import MobileSettings from "Container/BusinessSettings/Whatsapp/settings/Mobile";
import DesktopIntegrationDetail from "Container/BusinessSettings/Whatsapp/integrationDetails/Desktop";
import MobileIntegrationDetail from "Container/BusinessSettings/Whatsapp/integrationDetails/Mobile";
// import FacebookLeadIntegration from "Container/BusinessSettings/Whatsapp/facebookLeadIntegration/Desktop";
// import DesktopWidgetIntegration from "Container/BusinessSettings/WidgetIntegration/Desktop";

import Layout from "../Layout";

const WABusinessProfileSettings = (props) => {
  return (
    <Fragment>
      <Hidden xsDown implementation="js" only="xs">
        <Layout useFluid>
          <DesktopIntegrationDetail />
          {/* <FacebookLeadIntegration /> */}
          {/* <DesktopWidgetIntegration /> */}
        </Layout>
      </Hidden>
      <Hidden smUp implementation="js">
        {/* <MobileSettings waStatus={waStatus} /> */}
        <MobileIntegrationDetail />
      </Hidden>
    </Fragment>
  );
};

export default WABusinessProfileSettings;
